@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Montserrat", sans-serif;
  background-color: #1a1a1c;
  color: #fff;
}

.coin-app {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 64px;
  color: #fff;
}

.coin-search {
  margin-bottom: 64px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.coin-text {
  margin-bottom: 32px;
  text-align: center;
}

.coin-input {
  padding-left: 16px;
  width: 300px;
  height: 50px;
  border: none;
  border-radius: 5px;
  background: linear-gradient(to right, #ec008c, #fc6767);
  color: black;
  font-weight: 600;
}

.coin-data {
  width: auto;
}

.coin-input::-webkit-input-placeholder {
  color: black;
  font-weight: 600;
}

.coin-input:-ms-input-placeholder {
  color: black;
  font-weight: 600;
}

.coin-input::placeholder {
  color: black;
  font-weight: 600;
}

@media screen and (max-width: 576px) {
  * {
    width: auto;
    overflow-x: hidden;
  }
}

.coin-container {
  display: flex;
  justify-content: center;
}
.coin-row {
  display: flex;
  flex-direction: row;
  justify-content: start;
  align-items: center;
  height: 80px;
  border-bottom: 1px solid #888686;
  width: 900px;
}
.coin {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-right: 24px;
  width: 450px;
  text-align: start;
}
.coin h1 {
  font-size: 16px;
  width: 150px;
}

.coin img {
  height: 30px;
  width: 30px;
  margin-right: 10px;
}

.coin-symbol {
  text-transform: uppercase;
  width: 50px;
  margin-right: 20px;
}

.coin-price {
  width: 100px;
}

.coin-data {
  display: flex;
  text-align: center;
  justify-content: space-between;
  width: 100%;
  text-align: start;
}

.coin-volume {
  width: 200px;
}

.coin-marketcap {
  width: 240px;
}

.coin-percent {
  width: 80px;
}

.red {
  color: #f00606;
}

.green {
  color: #11d811;
}

@media screen and (max-width: 992px) {
  .coin-row {
    width: 100%;
  }
  .coin {
    width: 90%;
  }
  .coin h1 {
    width: 150px;
  }

  .coin-symbol {
    margin-right: 5px;
  }

  .coin-data {
    width: 420px;
    align-items: center;
  }

  .coin-price {
    width: 90px;
    visibility: visible;
  }
}

@media screen and (max-width: 768px) {
  .coin-row {
    width: 100%;
  }
  .coin {
    width: 90%;
  }
  .coin h1 {
    width: 100px;
  }

  .coin-symbol {
    margin-right: 5px;
  }

  .coin-data {
    width: 400px;
    align-items: center;
  }

  .coin-price {
    width: 90px;
    visibility: visible;
  }
}

@media screen and (max-width: 576px) {
  .coin-row {
    width: 100%;
  }
  .coin {
    width: 100%;
  }
  .coin h1 {
    width: 100px;
  }

  .coin-symbol {
    margin-right: 5px;
  }

  .coin-data {
    visibility: hidden;
    width: 0;
  }

  .coin-price {
    width: 90px;
    visibility: visible;
  }
}

